body {
    // position: relative;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    justify-content: space-between;
    padding: 0;
    height: 100%;
    font-family: 'Panton-Regular';
  }
  .page-container {
    max-width: 100%;
    min-height: 100vh;
    height: auto;
    flex: 1;
    margin-top: 60px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    padding: 50px 45px;
}

.title-container{
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    .label-title{
        text-transform: uppercase;
        font-size: 16px;
        color: #685D5B;
       
    }
    .title{
        font-family: 'Panton-Bold';
        text-transform: inherit;
        font-size: 31px;
        color: #685D5B;
       
    }
}

.title-container--light{
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    .label-title{
        text-transform: uppercase;
        font-size: 16px;
        color: white;
        font-family: 'Panton-Light';
       
    }
    .title{
        text-transform: uppercase;
        font-size: 31px;
        color: white;
       
    }
}

.bg-lighter{
    background-color: #fff;
    background-size: cover;
}
.bg-section{
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    
}
.bg-darkest{
    background-color: #22201F;
    background-size: cover;
}

.content-block{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    width: 100%;
    margin-top: 20px;
    font-size: 16px;
    color: #685D5B;
    
    margin-bottom: 65px;
    @media screen and (max-width:575.98px){
        flex-wrap: wrap;
    }
    @media screen and (min-width:576px)and(max-width:768px){
        flex-wrap: wrap;
    }
}

.content-block--2{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    width: 66%;
    margin-left: auto;
    margin-top: auto;
    margin-bottom: 0px;
    font-size: 16px;
    color: white;
    @media screen and (max-width:575.98px){
        width: 100%;
        flex-wrap: wrap;
        margin-left: 0px;
        margin-top: 0px;
        font-size: 10px;
        margin-bottom: 20px;
    }
    @media screen and (min-width:576px)and(max-width:768px){
        width: 100%;
        flex-wrap: wrap;
        margin-left: 0px;
    }
}

.content-block--3{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    width: 100%;
    font-size: 16px;
    color: white;
    font-family: 'Panton-Light';
    margin-bottom: 65px;
    
    @media screen and (max-width:575.98px){
        font-size: 14px;
    }
    @media screen and (min-width:576px)and(max-width:768px){
        width: 100%;
    }
}

.text-block--3{
    width: 80%;
    color: white;
    font-size: 16px;
    font-family: 'Panton-Light';
    padding-left: 18px;
}
.content-bottom{
    display: flex;
    flex-direction: column;
    margin-top: auto;
}
.list-team{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 5px;
    .container-team{
        display: flex;
        flex-direction: column;
        max-width: 200px;
        .avatar{
            width: 200px;
            height: auto;
        }
        .avatar-info{
            font-size: 16px;
            color: white;
            font-family: 'Panton-Regular';
            margin-top: 20px;
            h6{
                font-family: 'Panton-Bold';
            }
        }
    }
}
.label-title{
    font-family: 'Panton-Bold';
    text-transform: inherit;
}
.text--darkest{
    color: #685D5B;
    font-size: inherit;
    font-family: inherit;
}
.team-bg{
    background-repeat:no-repeat;
    background-size: 100%;
    max-height: 500px;
min-height: 300px;
}
#Team{
    display: block;
    @media screen and (max-width:575.98px){
        display: none;
    }
    @media screen and (min-width:576px)and(max-width:767.99px){
        display: none;
    }
    @media screen and (min-width:768px){
        display: block;
    }

}

#Team2{
    display: block;
    @media screen and (max-width:575.98px){
        display: block;
    }
    @media screen and (min-width:576px)and(max-width:767.99px){
        display: block;
    }
    @media screen and (min-width:768px){
        display: none;
    }

}
.contact-info{
    color: white;
    font-family: 'Panton-Regular';
    font-size: 16px;
    h6{
        font-family: 'Panton-Bold'; 
    }
}
.form-contact{
    background-color: transparent;
    border-radius: 0px;
}
.btn-lightest{
    color: #685D5B;
    background-color: white;
    border-color: white;
    font-family: 'Panton-Bold';
    border-radius: 0px;
}

.footer-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    font-family: 'Panton-Light';
    justify-content: space-between;
    .footer-copyrights{
        flex-direction: column;
        font-family: 'Panton-Light';
        p{
            font-family: 'Panton-Light'; 
            color: white; 
        }
        .footer-img{
            margin-top: 66px;
            width: 40px;
        }
    }
    .footer-nav{
        display: flex;
        flex-direction: row;
        .vr{
            height: auto;
            border-left: 1px solid white;
            @media screen and (max-width:575.98px){
                display: none;
            }
            @media screen and (min-width:576px)and(max-width:767.99px){
                display: none;
            }
        }
        .link-list{
            list-style-type: none;
            display: flex;
            flex-direction: column;
            .list-item{
                .link-footer{
                    font-family: 'Panton-Light';
                    color: white;
                    padding: 10px 20px;
                }
            }
        }
        
    }
    .footer-links{
        display: flex;
        flex-direction: row;
        .vr{
            height: auto;
            border-left: 1px solid white;
            @media screen and (max-width:575.98px){
                display: none;
            }
            @media screen and (min-width:576px)and(max-width:767.99px){
                display: none;
            }
        }
        .link-list{
            list-style-type: none;
            display: flex;
            flex-direction: column;
            .list-item{
                .link-footer{
                    font-family: 'Panton-Light';
                    color: white;
                    padding: 10px 20px;
                }
            }
        }
        
    }
}